.message{
    display: flex;
    margin-bottom: 10px;
  }
  
  
  .sent-bubble{
    margin-right: 0px;
    margin-left: auto;
    color: #FFFFFF;
  
    background: linear-gradient(90deg, #8867E7 0%, #E41E89 100%);
  
    text-shadow: 0px 0px 6px #000000;
  }
  .recieve-bubble{
    margin-right: auto;
    margin-left: 0px;
    color: var(--black-color);
    background: rgba(255, 255, 255, 0.7);
    /* Button Far Shadow */
  }
  
  .message-bubble{
    max-width: 250px;
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 125%;
    /* identical to box height, or 20px */
  
    display: flex;
    align-items: center;
  
    padding: 12px 20px;
    border-radius: 25px;
  
    
    backdrop-filter: blur(20px);
    box-shadow: 0px 20px 40px -10px rgba(146, 0, 170, 0.2), inset 1px 1px 0px #FFFFFF, inset -1px -1px 0px #d5d5d555;
  
    display: flex;
  }
  .waiting div{
    animation: fadeInOut 2s linear infinite;
    
  }

  @keyframes fadeInOut {
    0% {
      opacity: 0.1;
    }
    20% {
      opacity: 0.4;
    }
    100% {
      opacity: 0.1;
    }
  }