
.Chat {
    display: grid;
    grid-template-columns: auto 400px;
    place-items: center;
    height: 100vh;
    width: 100vw;
  }

  .side-bar header .menu-btn{
    display: none;
  }

  .notice{
    display: flex;
    width: 100%;
    justify-content: center;
    font-weight: bold;
    font-size: 1.5em;
    color: #979797;
  }
  
  .scroll-gradient-bottom{
    position: fixed;
    bottom: 0px;
    right: 0;
    left: 0;
    height: 100px;
    background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 50%, #FFFFFF 100%);
  }
  .scroll-gradient-top{
    position: fixed;
    top: 0px;
    right: 0;
    left: 0;
    height: 100px;
    background: linear-gradient(0deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 90%);
  }
  .mobile-header{
    display: none;
  }
  
  
  @media (max-width: 750px)
  {
    .Chat{
        grid-template-columns: auto;
      }

    .side-bar{
      transition: .5s ease-in-out;

      margin-top: 0px !important;
    }


    .menu-open{
      z-index: 10000;
      position: fixed;
      margin-top: 0px;
      top:0px;
      right:0px;
      left:0px;
      padding: 30px;
      width: calc(100vw - 60px);
      height: 90vh;
      margin: 0px;
      border-top-left-radius: 0px;
      border-top-right-radius: 0px;
      display: flex;
      flex-direction: column-reverse !important;

      padding-bottom: 10px !important;
     }
    .menu-closed{
      z-index: 10000;
      position: fixed;
      margin-top: 0px;
      top:calc(-90vh + 90px);
      right:0px;
      left:0px;
      padding: 30px;
      width: calc(100vw - 60px);
      height: 90vh;
      margin: 0px;
      border-top-left-radius: 0px;
      border-top-right-radius: 0px;
      display: flex;
      flex-direction: column-reverse !important;

      padding-bottom: 10px !important;
     }
     .side-bar header{
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      width: 100%;
     }
     .side-bar header .menu-btn{
      display: flex;
      justify-content: center;
      align-items: center;
      width: 50px;
      height: 50px;
      margin-right: -20px;
     }
     .side-bar header .menu-btn svg{
      padding: 0px;
      margin: 0px;
    }
    .mobile-header{
      display: flex !important;
      justify-content: center;
      background: rgba(255, 255, 255, 0.6);
    
      backdrop-filter: blur(20px);
      box-shadow: 0px 20px 40px -10px rgba(146, 0, 170, 0.2), inset 1px 1px 0px #FFFFFF, inset -1px -1px 0px #d5d5d555;
      position: fixed;
      top: 0px;
      left: 0px;
      right:0px;
      height: 60px;
      z-index: 5;
    }
    .mobile-header h2{
      font-family: Lobster;
      margin: auto;
    }
  }
  