
  .message-list{
    list-style: none;
    padding: 0px;
    margin: 0px;
  }
  .message-list li{
    list-style: none;
  }
  .message-list li:first-child{
    list-style: none;
    margin-top: 50px;
  }
  .message-list li:last-child{
    list-style: none;
    margin-bottom: 130px;
  }

  .chat-window{
    display: flex;
    flex-direction: column;
    height: 100vh;
    width: calc(100% - 140px);
    padding-left: 90px;
    padding-right: 50px;
    display: grid;
    overflow-y: scroll;
    scroll-behavior: smooth;
    
  }
  .chat-box{
    display: flex;
    flex-direction: column;
    margin-bottom: 0px;
    margin-top: auto;
  }
  
  .input-box{
    position: fixed;
    bottom: 50px;
    left: 90px;
    right: 450px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding:8px 30px;
    padding-right: 8px;
    /* White */
    z-index: 10;
    background: rgba(255, 255, 255, 0.6);
    /* Button Far Shadow */
    
    backdrop-filter: blur(20px);
    box-shadow: 0px 20px 40px -10px rgba(146, 0, 170, 0.2), inset 1px 1px 0px #FFFFFF, inset -1px -1px 0px #d5d5d555;
  
    border-radius: 35px;
  
  }
  
  .input-box .input-field{
    font-family: 'Inter', sans-serif;
    width: 100%;
    height: fit-content;
    overflow-y: auto;
    word-wrap:break-word;
    font-size: 14px;
    border: none;
    outline: none;
    background: none;
    margin-right: 30px;
    height: 14px;
    resize: none;
  }
  .input-box button{
    cursor: pointer;
    display: grid;
    place-items: center;
    padding: 0px;
    min-width: 50px;
    height: 50px;
  
    /* Main Accent Color */
  
    background: linear-gradient(90deg, #8867E7 0%, #E41E89 100%);
    /* Button Far Shadow */
  
    box-shadow: 0px 20px 40px -10px rgba(146, 0, 170, 0.2), inset 1px 1px 0px #FFFFFF, inset -1px -1px 0px #d5d5d555;
  
    border-radius: 45px;
    outline: none;
    border: none;
  }
  
  
  @media (max-width: 750px)
  {
    .App{
      grid-template-columns: auto;
    }
  
    .chat-window{
      padding: 30px;
      width: calc(100vw - 60px);
      padding-top: 0px;
      padding-bottom: 0px;
    }
    .input-box{
      position: fixed;
      bottom: 30px;
      left: 30px;
      right:30px;
    }
    .chat-box{
      padding-left: 0px;
      padding-right: 0px;

    }
    .message-list li:last-child{
      list-style: none;
      margin-bottom: 180px;
    }
  }
  
  .side-bar form{
    display: flex;
    align-items: center;
    flex-direction: column;
    gap: 30px;
  }
  .side-bar{
  
    margin-top: 50px;
    margin-bottom: 50px;
    margin-right: 90px;
    margin-left: 0px;
  
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    height: calc(100% - 150px);
    padding: 30px;
    gap: 10px;
    text-align: center;
    z-index: 10;
    background: rgba(255, 255, 255, 0.7);
    /* Button Far Shadow */
    
    backdrop-filter: blur(20px);
    box-shadow: 0px 10px 50px -25px rgba(146, 0, 170, 0.3), inset 1px 1px 0px #FFFFFF, inset -1px -1px 0px #d5d5d555;
  
    border-radius: 25px;
  }
  .profile-picture{
    background: rgb(229, 229, 229);
    padding: 0px;
    overflow: hidden;
    /* Button Far Shadow */
    
    backdrop-filter: blur(20px);
    box-shadow: 0px 10px 50px -25px rgba(146, 0, 170, 0.3), inset 1px 1px 0px #FFFFFF, inset -1px -1px 0px #d5d5d555;
  
    border-radius: 100%;
    height: 150px;
    width: 150px;
  }
  .profile-picture img{
    width: 100%;
  }
  .side-bar h2{
    font-family: Lobster;
    margin: 0;
  }
  .name-field{
    font-family: Lobster;
    margin: 0;
    border: none;
    outline: none;
    text-align: center;
    font-size: 1.5em;
    font-weight: bold;
  }
  .settings{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    text-align: center;
  }
  footer{
    
    display: grid;
    margin-left: 0px;
    margin-right: 0px;
    width: 100%;
  }
  .side-bar button{
    display: flex;
    grid-template-columns: 50px auto;
    place-items: center;
    margin-left: 0px;
    margin-right: 0px;
    color: #FFFFFF;
    cursor: pointer;
    display: grid;
    place-items: center;
    padding: 15px;
    text-shadow: 0px 0px 6px #000000;
    width: 100%;
  
    /* Main Accent Color */
  
    background: linear-gradient(90deg, #8867E7 0%, #E41E89 100%);
    /* Button Far Shadow */
  
    box-shadow: 0px 20px 40px -10px rgba(146, 0, 170, 0.2), inset 1px 1px 0px #FFFFFF, inset -1px -1px 0px #d5d5d555;
  
    border-radius: 45px;
    outline: none;
    border: none;
  }
  .side-bar button p{
    color: #FFFFFF;
    margin: 0px;
  }


.button-cover,
.knobs,
.layer {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.button {
  position: relative;
  font-family: Inter;
  width: 100px;
  height: 36px;
  overflow: hidden;
}

.button.r,
.button.r .layer {
  border-radius: 100px;
}

.button.b2 {
  border-radius: 2px;
}

.checkbox {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  opacity: 0;
  cursor: pointer;
  z-index: 3;
}

.knobs {
  z-index: 2;
}

.layer {
  width: 100%;
  background-color: #fcebf7;
  transition: 0.3s ease all;
  z-index: 1;
}

/* Button 3 */
#button-3 .knobs:before {
  content: "Female";
  position: absolute;
  top: 4px;
  left: 4px;
  width: fit-content;
  padding: 9px 15px;
  border-radius: 50px;
  height: 10px;
  color: #fff;
  font-size: 10px;
  font-weight: bold;
  text-align: center;
  line-height: 1;
  
  background-color: #E41E89;
  transition: 0.5s ease all, left 0.5s cubic-bezier(0.18, 0.89, 0.35, 1.15);
}

#button-3 .checkbox:active + .knobs:before {
  width: 60px;
  margin: auto;
}

#button-3 .checkbox:checked:active + .knobs:before {

  transition: .3s ease-in-out;
  width: 60px;
  margin: auto;
}

#button-3 .checkbox:checked + .knobs:before {
  content: "Male";
  left: auto;
  right: 4px;
  
  background-color: #8867E7;
}

#button-3 .checkbox:checked ~ .layer {
  
  
  background-color: #f0ebfc;
}