.Home{
    margin: 0px;
    padding: 50px;
    height: calc(100vh - 100px);
    display: grid;
    grid-template-rows: 40px calc(100vh - 100px - 150px) 100px;
}
.Home p{
    line-height: 150% !important;
}

.Home h1, .Home h2, .Home h3, .Home h4, .Home h5, .Home h6{
    margin: 0px;
    padding: 0px;
}

.Home header{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}
.Home header .logo{
    height: 50px;
    padding: 0px;
    margin: 0px;
    margin-left: -25px;
}

.Home button{
    z-index: 2;
    cursor: pointer;
    color: #FFFFFF;
    font-weight: bold;
    font-family: Inter, sans-serif;
    height: 50px;
    padding-left: 30px;
    padding-right: 30px;
    font-size: 16px;
    letter-spacing: 0.2em;
  
    background: linear-gradient(90deg, #8867E7 0%, #E41E89 100%);
  
    text-shadow: 0px 0px 6px #000000;
    /* Button Far Shadow */
  
    box-shadow: 0px 20px 40px -10px rgba(146, 0, 170, 0.2), inset 1px 1px 0px #FFFFFF, inset -1px -1px 0px #d5d5d555;
  
    border-radius: 45px;
    outline: none;
    border: none;
    transition: 500ms ease-in-out;
}
.Home button:hover{
    z-index: 2;
    cursor: pointer;
    color: #FFFFFF;
    font-weight: bold;
    font-family: Inter, sans-serif;
    height: 50px;
    padding-left: 30px;
    padding-right: 30px;
    font-size: 16px;
    letter-spacing: 0.2em;
  
    background: linear-gradient(90deg, #8867E7 0%, #E41E89 100%);
  
    text-shadow: 0px 0px 6px #000000;
    /* Button Far Shadow */
  
    box-shadow: 0px 5px 10px -5px rgba(146, 0, 170, 0.4), inset 1px 1px 0px #FFFFFF, inset -1px -1px 0px #d5d5d555;
  
    border-radius: 45px;
    outline: none;
    border: none;
}

.Home main{
    display: grid;
    grid-template-columns: 50% 50%;
    align-items: center;
    justify-content: space-between;
    gap: 30px;
}

.Home main .left{
    overflow: visible;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 30px;
    justify-content: left;
}

.Home h3{
    font-weight: 700;
    font-size: 1.5em;


}

.Home h1{
    font-family: "Lobster";
    font-size: 6em;
    line-height: 90%;
}

.Home h1 b, .Home h2 b, .Home h3 b, .Home h4 b, .Home h5 b,.Home h6 b{
    padding: 0px 10px;
    margin-left:-10px;
    background: linear-gradient(90deg, #8867E7 0%, #E41E89 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.Home main .left button{
    max-width: 450px;
}

.Home main .right{
    display: flex;
    margin-right: 0px;
    justify-content: flex-end;
}

.Home main .right img{
    margin-right: 0px;
    height: calc(100vh - 100px - 150px);
    margin-bottom: -50px;
}

.Home footer{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
    gap: 30px;
}

.Home footer ul{
    list-style: none;
    padding: 0px;
    margin: 0px;
    display: flex;
    flex-direction: row;
    gap: 30px;
}
.Home footer ul li {
    margin: 0px;
    padding: 0px;
    width: 250px;

}
.Home footer ul li span{
    display: grid;
    grid-template-columns: auto auto;
    justify-content: left;
    align-items: center;
    
}
.Home footer ul li h4{
    margin-left: 20px;

}
.Home footer p i{
    color: inherit;

}

@media (max-width: 1150px){
    .Home h1{
        font-family: "Lobster";
        font-size: 4.2em;
        line-height: 90%;
    }
    .Home h3{
        font-size: 1.2em;
    }

}
@media (max-width: 850px){

    .Home header .logo{
        height: 30px;
    }
    .Home button{
        font-size: 14px;
    }
    .Home h3 {
        font-size: 1.2em;
    }
    .Home{
        margin: 0px;
        padding: 0px;
        height: calc(100vh);
        display: flex;
        gap: 50px;
        flex-direction: column;
        overflow-y: scroll;
        text-align: center;
    }
    .Home header{
        margin: 0px 60px;
        margin-top: 30px;
        display: flex;
    }
    .Home main{
        display: flex;
        justify-content: center;
    }
    .Home main .right{
        display: none;
    }
    .Home .left{
        margin: 0 20vw;
        display: flex;
        gap: 10px;
        justify-content: center;
        align-items: center;
    }
    .Home footer{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .Home footer ul{
        flex-direction: column;
    }
    .Home footer ul li span{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: 0px;
    }
    .Home footer ul li span svg{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: auto auto;
        margin-bottom: 10px;
    }
    .Home footer ul li span h4{
        margin: 0px;
    }
}