@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100;400;500;700&family=Lobster&display=swap');


:root {
  --gray-color: #747474;
  --black-color: #1b1b1b;
}


*{
  /*
  
  box-shadow:0px 0px 0px 1px red inset;
  */
  transition: 100ms ease-in-out;
  color: var(--black-color);
}

p{
  color: var(--gray-color);
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 125%;
}
body {
  font-family: 'Inter', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin:0;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
